@media screen and (min-width:320px) {
    .container {
        position: absolute;
        top: 0;
        width: 100%;
        background-color: rgb(255, 255, 255);
        z-index: 1000;
        border-bottom: 1px solid #FF9100;
    }
    .content {
        padding: 1em 0.8em;
        display: flex;
        justify-content: space-between;
        align-items: center !important;
        position: relative !important;
        width: 100%;
        height: 100%;
    }
    .logo_content {
        text-decoration: none;
        font-size: 1.3em;
        color: #000;
        font-weight: 500;
        display: flex;
        align-items: flex-end;
        flex-wrap: nowrap;
    }
    .menu {
    display: none;
    justify-content: space-around;
    }
    .icon {
        margin-right: 0.4em;
    }
    .menu a {
        color: #000;
        text-decoration: none;
        margin: 0em 1.3em;
    }
    .menu p {
    margin: 0em 1.3em;
    }
    .border_left {
        width: 1px;
        height: 30px;
        background-color: #000;
    }
    .login {
        display: flex;
        align-items: center;
    }
    .btn_login {
        padding: 0.5em 0.8em;
        border-radius: 30px;
        border: 1px solid #FFC801;
        margin: 0em 1em !important;
        height: 100%;
        color: #000;
        text-decoration: none;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .login button:first-child {
    margin: 0em 0.2em 0em 0em;
    }
    .avatar {
        width: 30px;
        height: 30px;
        border-radius: 50%;
        margin: 0em 1em;
    }
    .cont_user {
        padding: 0.6em;
    }
    .link {
        display: block;
        width: 100% !important;
        text-decoration: none;
        color: #000;
        padding: 0.3em;
        margin-bottom: 1em;
    }
    .icon_home {
        font-size: 1.3em;
        color: #000;
        margin: 0em 0.9em;
    }
    .link_drop {
        background-color: transparent !important;
        border: none;
        color: #000 !important;
        padding: 0em;
    }
    .list {
        padding: 0em !important;
        margin: 0em !important;
    }
    .link_drop:hover {
        color: #000 !important;
    }
    .item {
        margin: 0em !important;
        padding: 0.5em;
    }
    .logout {
        width: 100% !important;
        display: flex;
        justify-content: flex-end;
    }
    .logout button {
        background-color:transparent;
        border: none;
        color: #000;
    }
    .content_login {
        background-color: #fafafa;
    }
    .email {
        font-size: 1em;
        margin-bottom: 1em !important;
    }
    .logo {
        height: 35px;
        display: block;
        margin-right: 0.2em;
    }
    .f_serif {
        font-family: 'PT Serif', serif;
        margin-bottom: -0.2em;
    }
    .g_2 {
        display: grid;
        grid-template-columns: 48% 48%;
        list-style-type: none;
        padding: 0em;
    }
    .ico {
        border: none !important;
        font-size: 1.1em;
        margin-left: 1em;
        color: rgba(2, 2, 2, 0.733);
    }
}
.icon_desk {
    display: none;
}
@media screen and (min-width:790px) {
    .menu {
        display: flex;
        justify-content: space-around;
        }
}
@media screen and (min-width:991px) {
    .icon_mobile {
        display: none;
    }
    .icon_desk {
        display: block;
    }
}
.width {
    width: 70% !important;
}